@charset "UTF-8";
.button,
input.button,
button.button {
  font-family: "Avenir LT W01 65 Medium", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 1rem;
  background: transparent;
  border: 2px solid currentColor;
  border-radius: 0;
  color: currentColor;
  cursor: pointer;
  display: inline-block;
  margin: 10px 0;
  padding: 5px 10px;
}

@media (min-width: 75em) {
  .button,
  input.button,
  button.button {
    font-size: 1.375rem;
  }
}

.button + button,
.button + .button,
input.button + button,
input.button + .button,
button.button + button,
button.button + .button {
  margin-left: 20px;
}

a.button {
  text-decoration: none;
}

.button--more {
  font-size: 1rem;
  font-family: "Avenir LT W01 85 Heavy", sans-serif;
  font-weight: 700;
  font-style: normal;
  border: 0;
  padding: 0;
}

@media (min-width: 75em) {
  .button--more {
    font-size: 1.125rem;
  }
}

.button--more::after {
  border: 1px solid currentColor;
  content: '➞';
  margin-left: 5px;
  padding: 0 5px;
}

a.button--more {
  text-decoration: none;
}

.button--link {
  font-size: 0.75rem;
  border: 0;
  margin: 20px 0 10px 0;
  padding: 0;
  text-transform: none;
}

@media (min-width: 48em) {
  .button--link {
    font-size: 0.9375rem;
  }
}

@media (min-width: 62em) {
  .button--link {
    font-size: 1.25rem;
  }
}

.button--link, .style--default .button--link {
  color: #36acef;
}

.button--link + .button {
  margin-left: 10px;
}

.button--link .fa:first-child {
  margin-right: 10px;
}

.button--link .fa:only-child {
  margin-right: 0;
}

@media (min-width: 62em) {
  .card--profile .button--link {
    font-size: 1rem;
  }
}

.buttons {
  margin-bottom: 10px;
  margin-top: 10px;
}

.fa {
  margin-right: 5px;
}

@media (min-width: 75em) {
