.image {
  line-height: 0;
  overflow: hidden;
}

.bg-image div {
  display: block;
}

@media (min-width: 48em) {
  .bg-image div {
    align-items: stretch;
    display: block;
    width: 100%;
  }
}

.bg-image img {
  display: block;
  flex: 1;
  object-fit: cover;
}

.bg-image {
  display: block;
  height: 100%;
  max-height: 90vh;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.bg-image .image__container {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  height: 100%;
  width: 100%;
}

.bg-image .image__container::before {
  content: '';
  display: block;
  padding-bottom: 75%;
  width: 100%;
}

a.paragraph--type--image img {
  width: 100%;
}

.card {
  background: #fff;
  color: #000;
}

@media (max-width: 29.99em) {
  .card {
    margin-bottom: 50px;
    text-align: justify;
  }
}

.card__title {
  font-family: "Avenir LT W01 85 Heavy", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 1.0625rem;
  margin-bottom: 5px;
  text-transform: none;
}

.card__title a {
  color: currentColor;
  text-decoration: none;
}

.style--default .card .card__title a {
  color: #000;
}

.card__media {
  margin-bottom: 20px;
}

.card__date {
  display: block;
  margin-bottom: 20px;
}

.card--profile {
  display: grid;
  grid-column-gap: 20px;
  grid-template-columns: 100px auto;
  grid-template-rows: 20px auto;
  padding: 20px 0;
  text-align: left;
}

@media (min-width: 48em) {
  .card--profile {
    display: block;
    margin-bottom: 0;
    padding-top: 0;
  }
}

.card--profile .card__media {
  grid-column: 1 / span 1;
  grid-row: 1 / span 2;
  margin-bottom: 0;
}

@media (min-width: 48em) {
  .card--profile .card__media {
    width: 100%;
  }
}

@media (min-width: 48em) {
  .card--profile .card__body {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media (min-width: 62em) {
  .card--profile .card__body {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.card--profile .card__title {
  color: #000;
  grid-column: 2 / span 1;
  grid-row: 1 / span 1;
}

@media (min-width: 48em) {
  .card--profile .card__title {
    font-size: 1rem;
  }
}

.card--profile .card__content {
  font-family: "Avenir LT W01 85 Heavy", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 0.75rem;
  color: #666;
  grid-column: 2 / span 1;
  grid-row: 2 / span 1;
}

@media (min-width: 48em) {
  .card--profile .card__content {
    font-size: 1rem;
  }
}

@media (min-width: 48em) {
  .card--profile .card__content {
    font-family: "Avenir LT W01 65 Medium", sans-serif;
    font-weight: 500;
    font-style: normal;
  }
}

.layout__container .layout__item.paragraph--type--card .card--article {
  padding-bottom: 30px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 30px;
}

.tout {
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  height: 100%;
