}

.header {
  padding-bottom: 30px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 30px;
}

.header, .header.style--default {
  background-color: #f5f5f5;
}

@media (min-width: 48em) {
  .header {
    margin-bottom: 0;
    padding-left: 0;
    padding-right: 0;
  }
}

.header .layout__container {
  padding-left: calc(((100% - 330px) / 12 * 1));
  padding-right: calc(((100% - 330px) / 12 * 1));
}

@media (min-width: 48em) {
  @supports (display: grid) {
    .header .layout__container {
      align-items: center;
      grid-gap: 0 30px;
    }
  }
}

.header__link {
  display: none;
}

@media (min-width: 48em) {
  .header__link {
    display: inline-block;
    float: right;
  }
}

.list {
  display: inline-block;
  padding: 20px;
}

.list, .list.style--default {
  background-color: #fff;
}

.list__title {
  font-family: "Avenir LT W01 95 Black", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 1.125rem;
}

.list__items {
  list-style: none;
  padding-left: 0;
}

.list__item {
  margin-bottom: 15px;
}

.list__item a {
  font-family: "Avenir LT W01 85 Heavy", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 1rem;
}

.hero {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  height: 380px;
  max-width: 100%;
  min-height: 280px;
  overflow: hidden;
  position: relative;
  width: 100%;
}

@media (min-width: 48em) {
  .hero {
    height: 450px;
  }
}

@media (min-width: 62em) {
  .hero {
    height: 500px;
  }
}

@media (min-width: 75em) {
  .hero {
    height: 650px;
  }
}

.hero .hero__container {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: flex-start;
  position: absolute;
  width: 100%;
}

.hero .hero__content {
  margin: 0;
  overflow: hidden;
  padding: 0 calc(30px + ((100% - 330px) / 12 * 2));
  z-index: 1;
}

@media (min-width: 48em) {
  .hero .hero__content {
    padding: 0 calc(((100% - 330px) / 12 * 1));
  }
}

@media (min-width: 62em) {
  .hero .hero__content {
    padding: 0 calc(90px + ((100% - 330px) / 12 * 4)) 0 calc(((100% - 330px) / 12 * 1));
  }
}

.hero .hero__content h1 {
  margin: 0;
}

.hero-has-video .hero__video {
  bottom: 0;
  height: 100%;
  left: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 0;
}

@media (max-width: 61.99em) {
  .hero-has-video .hero__video {
    display: none;
  }
}

.hero-has-video .hero__video iframe {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

@media (min-width: 48em) {
  .hero-style-default .hero__content {
    padding-left: calc(((100% - 330px) / 12 * 1));
    padding-right: calc(120px + ((100% - 330px) / 12 * 4));
  }
}

@media (min-width: 48em) {
  .hero-style-right .hero__container {
    align-content: flex-end;
    justify-content: flex-end;
  }
}

@media (min-width: 48em) {
  .hero-style-right .hero__content {
    padding-left: calc(150px + ((100% - 330px) / 12 * 5));
    padding-right: calc(((100% - 330px) / 12 * 1));
  }
}

@media (min-width: 48em) {
  .hero-style-left .hero__container {
    align-content: flex-start;
    justify-content: flex-start;
  }
}

@media (min-width: 48em) {
  .hero-style-left .hero__content {
    padding-left: calc(((100% - 330px) / 12 * 1));
    padding-right: calc(150px + ((100% - 330px) / 12 * 5));
  }
}

.hero-style-article {
  max-height: 350px;
}

.hero-style-article .hero__content {
  padding: 0 calc(30px + ((100% - 330px) / 12 * 1));
}

@media (min-width: 62em) {
  .hero-style-article .hero__content {
    margin-left: 15%;
    padding: 0;
    width: 85%;
  }
}

.hero-style-short {
